.members_wrap {
    height: calc(100vh - 1rem);
    overflow-y: scroll;
}
.banner {
    height: 2.22rem;
    width: 100vw;
    background: url(../../../assets/images/banner@2x.png) center center / cover no-repeat;
}
.members_bg {
    height: 13.3rem;
    background: url(../../../assets/images/members_bg@2x.png) center center / cover no-repeat;
}
.members_button {
    height: 1rem;
    font-size: .32rem;
    font-weight: bold;
    color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background: -webkit-gradient(linear, left top, right top, from(#f85834), to(#fe992c));
    background: -webkit-linear-gradient(left, #f85834, #fe992c);
    background: -o-linear-gradient(left, #f85834, #fe992c);
    background: linear-gradient(to right, #f85834, #fe992c);
}


